import React from 'react'
import './style.css'
import { FormattedDate, FormattedNumber, FormattedPlural,useIntl } from "react-intl";
import { FormattedMessage } from "react-intl";

/**
* @author
* @function SocialConnect
**/

const SocialConnect = (props) => {
  const intl = useIntl();

  return(
    <div className="socialConnect" style={props.style}>
        <span><FormattedMessage id="folllow_me_on" /> </span>
        <a className="socialLink" href={props.href}>
        <i class="fa fa-linkedin-square" style={{fontSize:'30px'}}></i>
        </a>
    </div>
   )

 }

export default SocialConnect