import React from 'react'
import { CircularProgressbar,buildStyles } from 'react-circular-progressbar'
import 'react-circular-progressbar/dist/styles.css'
import "./style.css"

/**
* @author
* @function Skill
**/

const Skill = (props) => {

  return(
      <div className="flexRow  mtb-10" style={{marginLeft:'30%'}}>
          <div className="skillContainer">
            <CircularProgressbar
                value= {props.value}
                text = {`${props.value}%`}
                styles={buildStyles({
                    textColor:props.textColor,
                    pathColor :props.pathColor
                })}/> 
          </div>
            <div className="mlr-10">
                <p className="font-16 bold-500 mtb-10">{props.skillName}</p>
                <p className="font-14  bold-500 grey mtb-10">{props.description}</p>
            </div>
      </div>
    )

 }

export default Skill