import React from 'react'
import SmallHeading from '../UI/SmallHeading'
import Card from '../UI/Card'
import capgemini from '../../assets/images/capgemini.png'
import berexia from '../../assets/images/berexia.png'
import involys from '../../assets/images/involys.png'
import aevaweb from '../../assets/images/aevaweb.png'
import { FormattedDate, FormattedNumber, FormattedPlural,useIntl } from "react-intl";
import { FormattedMessage } from "react-intl";
import Experience from '../UI/Experience'

/**
* @author
* @function Experiences
**/

const Experiences= (props) => {
  const intl = useIntl();

  const experiences = [
    {
      link :"https://www.capgemini.com/",
      imgPath :capgemini,
        job : <FormattedMessage id="developer"values={{ developer: "full stack React js/Asp.net core web api" }} />,
        delay :<FormattedMessage id="from"values={{ from: "05/2021"}} />,
        societe :"Capgemini",
        city : 'Casablanca/MAROC',
        tasks :[intl.formatMessage({id: 'cap_task1'}),intl.formatMessage({id: 'cap_task2'})]
    },
    {
      link:"http://www.berexia.com/",
      imgPath :berexia,
        job :<FormattedMessage id="developer"values={{ developer: "full stack Angular js/Asp.net web api" }} />,
        delay :<FormattedMessage id="from_to"values={{ from: "08/2020",to :"05/2021" }} />,
        societe :"Berexia",
        city : 'Casablanca/MAROC',
        tasks :[intl.formatMessage({id: 'ber_task1'}),intl.formatMessage({id: 'ber_task2'}),intl.formatMessage({id: 'ber_task3'}),intl.formatMessage({id: 'ber_task4'})
        ]
    },
    {
      link:"https://involys.com/",
      imgPath :involys,
        job : <FormattedMessage id="developer"values={{ developer: "full stack Angular/Asp.net core" }} />,
        delay :<FormattedMessage id="from"values={{ from: "02/2020", to:"06/2020"}} />,
        societe :"Involys",
        city : 'Casablanca/MAROC',
        tasks :[intl.formatMessage({id: 'inv_task1'}),intl.formatMessage({id: 'inv_task2'}),intl.formatMessage({id: 'inv_task3'})]
    },
    {
      link:"http://www.aevaweb.com/",
      imgPath :aevaweb,
        job :<FormattedMessage id="developer"values={{ developer: "full stack Angular 7/Nodejs" }}/>,
        delay : <FormattedMessage id="from"values={{ from: "07/2019", to:"08/2019"}} />,
        societe :"Aevaweb",
        city : 'Oujda/MAROC',
        tasks :[intl.formatMessage({id: 'aev_task1'}),intl.formatMessage({id: 'aev_task2'}),intl.formatMessage({id: 'aev_task3'})]
    }
]
  return(
    <div className="container" style={{marginTop:"30px"}} data-aos="zoom-in-up">
        <Card style={{paddingTop:'30px'}} >
            <div className="mtb-10">
                <SmallHeading text={intl.formatMessage({id: 'experiences'})}/>
                
            </div>
            <div className="mtb-10 justify-sb" >
            {
                experiences.map((experience,index) =>  <Experience
                key ={index}
                link = {experience.link}
                imgPath={experience.imgPath}
                job = {experience.job}
                delay = {experience.delay}
                societe={experience.societe}
                city={experience.city}
                tasks={experience.tasks.map((task,index)=><ul className="mlr-10">
                  <li>{task}</li>
                </ul>)}/>)
            }
        </div>
        </Card>
    </div>
   )

 }

export default Experiences