import React from 'react'
import SmallHeading from '../UI/SmallHeading'
import Title from '../UI/Title'
import ensa from '../../assets/images/ensa.png'
import university from '../../assets/images/university.png'
import { FormattedDate, FormattedNumber, FormattedPlural,useIntl } from "react-intl";
import { FormattedMessage } from "react-intl";
/**
* @author
* @function Qualification
**/

const Qualification = (props) => {
  const intl = useIntl();

    const diplomas = [
        {
          title :<FormattedMessage id="ingeneer_diploma"/>,
          mediumTitle :<FormattedMessage id="ensa" />,
            desc :<FormattedMessage id="from_to"values={{ from: "09/2015",to:"07/2020"}} />,
            path:ensa,
            link:"https://www.ensamaroc.com/"


                },
                {
                    title :<FormattedMessage id="unive_diploma"/>,
                    mediumTitle :<FormattedMessage id="unive_name" />,
                      desc : <FormattedMessage id="from_to"values={{ from: "09/2013",to:"07/2015"}} />,
                      path:university,
                      link:"http://www.umi.ac.ma/"

                          },
                          {
                            title :<FormattedMessage id="bac"/>,
                            mediumTitle :<FormattedMessage id="physical_science" />,
                              desc :<FormattedMessage id="from_to"values={{ from: "09/2012",to:"07/2013"}} />,
                                  },
     
    ]
  return(
    <div className="container" data-aos="zoom-out-up" >
        <SmallHeading text={intl.formatMessage({id: 'education'})}/>
      
        <div style={{display :'flex',flexDirection:'column',alignItems:'center'}}>
        {diplomas.map((diploma,index) =>  <Title
                key ={index}
                title={diploma.title}
                mediumTitle={diploma.mediumTitle}
                desc={diploma.desc}
                path={diploma.path}
                link={diploma.link}/>)

        }
        </div>
    </div>
   )

 }

export default Qualification