import React from 'react'
import { colors } from '../../style'
import Card from '../UI/Card'
import MediumHeading from '../UI/MediumHeading'
import Skill from '../UI/Skill'
import SmallHeading from '../UI/SmallHeading'
import { FormattedDate, FormattedNumber, FormattedPlural,useIntl } from "react-intl";
import { FormattedMessage } from "react-intl";

/**
* @author
* @function Specializing
**/

const Specializing = (props) => {
    const intl = useIntl();

    const skills = [
        {
            skillName :'HML/CSS/BOOTSTRAP/JAVASCRIPT',
            description : 'Front end',
            value :60,
            textColor :colors.primaryColor,
            pathColor : '#118841'
        },        
        {
            skillName :'REACTJS/ANGULAR',
            description : 'Framework js',
            value :60,
            textColor :colors.primaryColor,
            pathColor : '#100911'
        },
        {
            skillName :'C#/Asp.Net (core)/VISUAL STUDIO',
            description : 'Back end ',
            value :60,
            textColor :colors.primaryColor,
            pathColor : '#369991'
        },
        {
            skillName :'SQL/MONGODB',
            description :<FormattedMessage id="data_base" />,
            value :60,
            textColor :colors.primaryColor,
            pathColor : '#999991'
        },
        {
            skillName :'XUNIT/KARMA',
            description : <FormattedMessage id="unit_test" />,
            value :80,
            textColor :colors.primaryColor,
            pathColor : '#544991'
        }


    ]
  return(
    <div className="container" >
        <Card style={{padding:'30px'}}>
        <SmallHeading text={intl.formatMessage({id: 'what_I_do'})}/>
        <MediumHeading text={intl.formatMessage({id: 'specialized_in'})}/>
        <div data-aos="fade-up" className="mtb-10" >
            {
                skills.map((skill,index) =>  <Skill 
                key ={index}
                skillName={skill.skillName}
                description = {skill.description}
                value = {skill.value}
                textColor={skill.textColor}
                pathColor={skill.pathColor}/>)
            }
        </div>
        <div>
          
        </div>
        </Card>
       
    </div>
   )

 }

export default Specializing